* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

button:focus {
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
p {
  margin: 0;
}

ul {
  margin: 0 !important;
}

@keyframes spin {
  from {
    transform: translate(-50%, -50%) rotate(0deg);
  }

  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

header {
  background: linear-gradient(to top, #000000, #320d16 40%, #490515, #660011, #8b0000);
}

header .nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.red_line {
  position: relative;
  z-index: 10;
  width: 100%;
  height: 2px;
  background: #ff0000;
  box-shadow: 0 0px 20px 3px red;
  animation: red_line 4s linear infinite;
}

@keyframes red_line {
  0% {
    background: #ff0000;
    box-shadow: 0 0px 20px 3px #ff0000;
  }

  50% {
    background: #420000;
    box-shadow: 0 0px 20px 3px #420000;
  }

  100% {
    background: #ff0000;
    box-shadow: 0 0px 20px 3px #ff0000;
  }
}

.banner {
  padding: 20px 0 0 0;
  background: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.banner .container {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
  color: #ffffff;
}

.banner .container .banner_div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 67%;
  flex-wrap: wrap;
  padding: 40px 0px;
  background: rgba(0, 0, 0, 0.445);
  margin-bottom: 40px;
}

.banner .container .banner_div h1 {
  font-size: 60px;
  text-transform: uppercase;
  font-family: 'Rhodium Libre', serif;
  text-shadow: 0 0 10px #FFFFFF;
  line-height: 60px;
  margin-bottom: 10px;
}

.banner .container .banner_div .word_group {
  font-size: 70px;
  margin-left: 10px;
}

.banner .container .banner_div .banner_line {
  width: 60%;
  height: 1px;
  background: #ffffff;
  box-shadow: 0 0 10px 1px white;
}

.banner .container .banner_div p {
  margin-top: 20px;
  font-size: 15px;
  color: white;
  padding: 0 30px;
  font-family: 'Rubik', sans-serif;
}

.banner img {
  width: 100%;
}

.merts {
  -webkit-animation: merts 0.6s infinite linear;
  animation: merts 0.6s infinite linear;
  margin-left: 2px;
  font-size: 17px;
}

@-webkit-keyframes merts {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

/* Main styles */

@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 800;
  src: url(https://fonts.gstatic.com/s/opensans/v18/mem5YaGs126MiZpBA-UN8rsOUuhs.ttf) format('truetype');
}

.text {
  font-family: 'Rhodium Libre', serif;
  font-size: 200px;
  fill: none;
  stroke-width: 6;
  stroke-linejoin: round;
  stroke-dasharray: 70 330;
  stroke-dashoffset: 0;
  -webkit-animation: stroke 6s infinite linear;
  animation: stroke 6s infinite linear;
}

.text:nth-child(5n + 1) {
  stroke: #ffffff;
  -webkit-animation-delay: -1.2s;
  animation-delay: -1.2s;
}

.text:nth-child(5n + 2) {
  stroke: #ff0000;
  -webkit-animation-delay: -2.4s;
  animation-delay: -2.4s;
}

.text:nth-child(5n + 3) {
  stroke: #ffffff;
  -webkit-animation-delay: -3.6s;
  animation-delay: -3.6s;
}

.text:nth-child(5n + 4) {
  stroke: #ff0000;
  -webkit-animation-delay: -4.8s;
  animation-delay: -4.8s;
}

.text:nth-child(5n + 5) {
  stroke: #ffffff;
  -webkit-animation-delay: -6s;
  animation-delay: -6s;
}

@-webkit-keyframes stroke {
  100% {
    stroke-dashoffset: -400;
  }
}

@keyframes stroke {
  100% {
    stroke-dashoffset: -400;
  }
}

/* Other styles */

.about_us {
  display: flex;
  width: 100%;
  background: linear-gradient(-45deg, #000000, #000000, #202020, #000000);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  padding: 50px 0;
}

.about_us .bok {
  width: 20%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  animation: bok_anim 10s linear infinite;
}

.about_us .bok img {
  width: 40%;
  animation: bok_img 10s linear infinite;
}

.about_us .about {
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.about_us .about h2 {
  font-family: 'Rubik', sans-serif;
  font-size: 36px;
  color: #FF0000;
  padding: 0px 10px;
  border-bottom: 3px red solid;
  margin-bottom: 30px;
}

.about_us .about .about_div {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}

.about_us .about .about_div .about_img {
  height: 320px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.about_us .about .about_div .about_img .image_bg_shadow {
  box-shadow: 0 0 30px 10px #660000;
  animation: red_shadow 4s linear infinite;
  min-width: 310px;
  min-height: 310px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url(../img/ezgif.com-gif-maker.gif) 50% 50% / cover;
}

.about_us .about .about_div .about_img img {
  width: 90%;
  height: 90%;
  animation: animation_img 6s linear infinite;
}

.about_us .about .about_div .about_text {
  color: white;
  margin-right: 45px;
}

.about_us .about .about_div .about_text h3 {
  text-shadow: 0 0 10px red;
  font-size: 30px;
  font-family: 'Rubik', sans-serif;
  margin-bottom: 20px;
}

.about_us .about .about_div .about_text p {
  text-align: justify;
  font-size: 18px;
  font-family: 'Rubik', sans-serif;
  font-weight: 400;
}

.about_us .about .about_div .about_text:nth-of-type(even) {
  color: white;
  margin-right: 0px;
  margin-left: 45px;
}

.about_us .about .about_div .about_text:nth-of-type(even) h3 {
  text-shadow: 0 0 10px red;
  font-size: 30px;
  font-family: 'Rubik', sans-serif;
  margin-bottom: 20px;
}

.about_us .about .about_div .about_text:nth-of-type(even) p {
  text-align: justify;
  font-size: 18px;
  font-family: 'Rubik', sans-serif;
  font-weight: 400;
}

.about_us .about .about_div:nth-of-type(3) {
  display: none;
}

.about_us .about .title {
  margin-top: 20px;
}

.about_us .about .our_works {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.about_us .about .our_works .work {
  width: 30%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
}

.about_us .about .our_works .work img {
  width: 100%;
}

.price_list {
  padding: 50px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.price_list h2 {
  font-family: 'Rubik', sans-serif;
  font-size: 36px;
  color: #ffffff;
  padding: 0px 10px;
  border-bottom: 3px #ffffff solid;
  margin-bottom: 30px;
}

.price_list .container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.price_list .price {
  position: relative;
  width: 100%;
  text-align: justify;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 82px 50px 62px 50px;
}

.price_list .cost {
  width: 100%;
  padding: 30px 0;
  background: blue;
  backdrop-filter: blur(50px);
  background-attachment: fixed;
  border-radius: 25px;
  margin: 40px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.price_list .cost h4 {
  text-transform: uppercase;
  font-size: 25px;
  color: #ffc400;
  font-family: 'Rubik', sans-serif;
}

.price_list .txt-bg {
  position: absolute;
  width: 182px;
  height: 182px;
}

.price_list .txt-bg:nth-of-type(1) {
  animation: txt_bg 2s infinite linear;
  top: 0;
  left: 0;
  border-top: 10px solid #FF0000;
  border-left: 10px solid #FF0000;
}

.price_list .txt-bg:nth-of-type(2) {
  animation: txt_bg2 2s infinite linear;
  animation-delay: 1s;
  bottom: 0;
  left: 0;
  border-bottom: 10px solid #ffffff;
  border-left: 10px solid #ffffff;
}

.price_list .txt-bg:nth-of-type(4) {
  animation: txt_bg2 2s infinite linear;
  animation-delay: 1s;
  top: 0;
  right: 0;
  border-top: 10px solid #ffffff;
  border-right: 10px solid #ffffff;
}

.price_list .txt-bg:nth-of-type(5) {
  animation: txt_bg 2s infinite linear;
  bottom: 0;
  right: 0;
  border-bottom: 10px solid #FF0000;
  border-right: 10px solid #FF0000;
}

.price_list .price_txt {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.price_list .price_txt .vid {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 10px;
  min-width: 312px;
  max-width: 312px;
  background: url(../img/grey-geometrical-shapes-background.jpg) 50% 50% / cover;
  padding: 20px;
  background-attachment: fixed;
  color: #ffffff;
  font-family: 'Rubik', sans-serif;
  border-top: 2px red solid;
  border-left: 2px red solid;
  border-bottom: 2px #ffffff solid;
  border-right: 2px #ffffff solid;
  margin-bottom: 20px;
}

.price_list .price_txt .vid h3 {
  font-size: 24px;
  margin-bottom: 20px;
  border-bottom: 2px #ff0000 solid;
}

.price_list .price_txt .vid P {
  text-align: left;
}

@keyframes txt_bg2 {
  0% {
    border-color: #ffffff;
  }

  50% {
    border-color: rgba(255, 0, 0, 0);
  }

  60% {
    border-color: rgba(255, 0, 0, 0);
  }

  100% {
    border-color: #ffffff;
  }
}

@keyframes txt_bg {
  0% {
    border-color: #ff0000;
  }

  50% {
    border-color: rgba(0, 0, 0, 0);
  }

  60% {
    border-color: rgba(0, 0, 0, 0);
  }

  100% {
    border-color: #ff0000;
  }
}

