@keyframes bok_anim {
  0% {
    padding-top: 0;
  }

  50% {
    padding-top: 100px;
  }

  100% {
    padding-top: 0;
  }
}

@keyframes bok_img {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

@keyframes red_shadow {
  0% {
    box-shadow: 0 0 30px 10px #660000;
  }

  50% {
    box-shadow: 0 0 30px 10px #ff0000;
  }

  100% {
    box-shadow: 0 0 30px 10px #660000;
  }
}

@media (max-width: 1300px) {
  .about_us {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px 35px;
  }

  .about_us .bok {
    display: none;
  }

  .about_us .about {
    width: 100%;
  }
}

@media (max-width: 900px) {
  .about_us .about h2 {
    font-size: 33px;
  }

  .about_us .about .about_div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
    margin-bottom: 30px;
  }

  .about_us .about .about_div .about_img {
    height: 320px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin-bottom: 20px;
  }

  .about_us .about .about_div .about_text {
    margin-right: 0px;
    margin-left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .about_us .about .about_div .about_text h3 {
    text-shadow: 0 0 10px red;
    font-size: 30px;
    font-family: 'Rubik', sans-serif;
    margin-bottom: 20px;
    text-align: center;
  }

  .about_us .about .about_div .about_text p {
    text-align: justify;
  }

  .about_us .about .about_div .about_text:nth-of-type(even) {
    margin-right: 0px;
    margin-left: 0;
  }

  .about_us .about .about_div:nth-of-type(2) {
    display: none;
  }

  .about_us .about .about_div:nth-of-type(3) {
    display: flex;
  }
}

@media (max-width: 800px) {
  .banner .container .banner_div {
    width: 100%;
  }

  .text {
    font-size: 160px;
  }
}

@media (max-width: 600px) {
  .text {
    font-size: 120px;
  }
}